.home-page {
    background-color: rgb(187, 100, 0);
    background-size: cover;
    background-image: url('../../../../images/backgrounds/main_background.png');
    border-bottom: 8px solid #FFA800;
    vertical-align: middle;
}

.home-header {
    display: flex;
    flex-wrap: wrap;
}

.header-title {
    margin-left: 14%;
    padding-top: 400px;
    font-weight: 100;
    font-size: 72px;
    line-height: 84px;
    color: #FFFFFF;
}

.header-title__first-part {
    font-weight: lighter;
}
