.reviews {
    list-style: none;
    justify-content: center;
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    margin: 50px auto 290px;
}

.reviews__title {
    margin-left: 250px;
    margin-top: 145px;
    margin-bottom: 50px;
    text-transform: uppercase;
    font-weight: 500;
    font-size: 36px;
    line-height: 42px;
}

.reviews__selector {
    position: relative;
    bottom: 120px;
    margin-left: 50%;
}

.reviews__selector-img {
    margin-right: 10px;
    cursor: pointer;
}

.review {
    display: flex;
    margin-left: 10%;
    margin-bottom: 50px;
}

.review__image {
    border-radius: 100px;
    width: 200px;
    height: 200px;
    margin-right: 35px;
}

.review__name {
    margin-bottom: 25px;
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    line-height: 28px;
    color: #000000;
}

.review__text {
    width: 400px;
    font-weight: 300;
    font-size: 24px;
    line-height: 28px;
    color: #928F94;
}
