.how-it-works {
    background-image: url('../../../../images/backgrounds/how-it-works-background.png');
    background-color: rgb(187, 100, 0);
    background-size: cover;
    height: 575px;
    vertical-align: middle;
}

.how-it-works__title {
    margin-left: 10%;
    margin-bottom: 30px;
    padding-top: 70px;
    text-transform: uppercase;
    font-weight: 500;
    font-size: 36px;
    line-height: 42px;
    color: #FFFFFF;
    position: relative;
}

.how-it-works__learn-more {
    position: relative;
    bottom: 80px;
    width: 325px;
    height: 60px;
    margin-left: 60%;
    background: transparent;
    border-radius: 7px;
    border: 1px solid #FFFFFF;
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 28px;
    text-align: center;
    color: #FFFFFF;
    cursor: pointer;
}

.how-it-works__learn-more:hover {
    box-shadow: 0 4px 4px rgb(104, 97, 97);
}

.how-it-works__elements {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
}

.how-it-works__element-image {
    display: block;
    margin: 0 auto;
}

.how-it-works__element-text {
    width: 280px;
    height: 72px;
    margin-top: 34px;
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 28px;
    text-align: center;
    color: #FFFFFF;
}